/* =============== RESET =============== */
* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
// :focus,
// :active {
// 	outline: none;
// }
// a:focus,
// a:active {
// 	outline: none;
// }
nav,
footer,
header,
aside,
section,
main {
	display: block;
}
html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea,
select {
	font-family: inherit;
	font-size: inherit;
}
input::-ms-clear {
	display: none;
}
button,
input[type="submit"] {
	display: inline-block;
	background: none;
	box-shadow: none;
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
// input:focus,
// input:active,
// button:focus,
// button:active {
// 	outline: none;
// }
label {
	cursor: pointer;
}
legend {
	display: block;
}
a {
	color: inherit;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img,
svg,
video {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
}
p + p {
	margin: 1em 0 0 0;
}
